import React from 'react'
import PageComponent from '../../components/page-component'
import {
  logo,
  title
} from '@marscollective/gatsby-theme-core/metadata.temp.json'

const LinksPreview = ({ entry, getAsset }) => {
  const { data } = entry.getIn(['data']).toJS()

  return <PageComponent links={data} logo={getAsset(logo)} title={title} />
}

export default LinksPreview
