import {
  init,
  registerPreviewTemplate as template,
  registerWidget
} from 'netlify-cms-app'
import { Control as SliderControl } from './slider'
import LinksPreview from './templates/links'
import NotFoundPreview from './templates/not-found'
import MetadataPreview from './templates/metadata'
import Pages from './collections/pages'
import Settings from './collections/settings'
import { siteUrl } from '@marscollective/gatsby-theme-core/metadata.temp.json'

registerWidget('slider', SliderControl)
template('links', LinksPreview)
template('not-found', NotFoundPreview)
template('siteMetadata', MetadataPreview)

init({
  config: {
    backend: {
      name: 'git-gateway',
      branch: 'main',
      commit_messages: {
        create: '📝 Create “{{slug}}”',
        update: '📝 Update “{{slug}}”',
        delete: '🔥 Delete “{{slug}}”',
        uploadMedia: '🍱 Upload “{{path}}” [skip ci]',
        deleteMedia: '🔥 Delete “{{path}}” [skip ci]'
      }
    },
    local_backend: {
      url: 'http://localhost:8000/api/v1'
    },
    media_folder: 'static/images',
    public_folder: 'images',
    locale: 'en',
    display_url: siteUrl,
    collections: [Pages, Settings]
  }
})
