import React from 'react'
import NotFoundComponent from '../../components/not-found-component'
import {
  logo,
  title as pageTitle
} from '@marscollective/gatsby-theme-core/metadata.temp.json'

const NotFoundPreview = ({ entry, getAsset }) => {
  const { buttonLabel, message, title } = entry.getIn(['data']).toJS()

  return (
    <NotFoundComponent
      buttonLabel={buttonLabel}
      logo={getAsset(logo)}
      message={message}
      pageTitle={pageTitle}
      title={title}
    />
  )
}

export default NotFoundPreview
